/* 
	My custom settings. These variables are copied from _settings.css
	This file will only contain uncommented settings that are actually used in the theme.
*/
.as-contact-form {
  margin-bottom: 2rem; }
  .as-contact-form label {
    padding-bottom: 0.3rem;
    font-size: 1rem; }
  .as-contact-form input[type="text"], .as-contact-form textarea {
    border-width: 3px; }
  .as-contact-form textarea {
    min-height: 5rem; }
  .as-contact-form input[type="submit"] {
    min-width: 5rem;
    border-radius: 8px;
    box-shadow: 0 4px 0 0 #49bfdc;
    background-color: #74CEE4;
    color: #FFFFFF;
    font-weight: bold;
    font-size: 1.3rem;
    cursor: pointer;
    display: block;
    width: 100%;
    padding: 0.3rem 0;
    margin-bottom: 0; }
    .as-contact-form input[type="submit"]:hover {
      background-color: #56c3de;
      margin-top: 4px;
      margin-bottom: -4px; }
    .as-contact-form input[type="submit"]:active {
      background-color: #49bfdc;
      margin-top: 4px;
      margin-bottom: -4px; }
    .as-contact-form input[type="submit"]:hover, .as-contact-form input[type="submit"]:active {
      margin-bottom: 0; }

/**
 * [jumbotron: the headline component for intro pages]
 * @param  {[string]} $header-img [background image for headlines]
 * @param  {[string]} $stamp-img  [background image for stamp image]
 * @dependencies {[mixin]} stamp-image
 */
/* -------------------------------------------- \
  About template layouts settings
\ --------------------------------------------- */
.page .page-jumbotron-container {
  position: relative;
  height: 34vh;
  margin-bottom: 10rem;
  background-image: url('../../assets/img//headlines/children.jpg');
  background-size: 100%;
  background-position: 50% 50%; }
  .page .page-jumbotron-container .page-title {
    position: absolute;
    top: 37vh;
    left: 26rem;
    margin-top: 0;
    font-size: 3rem;
    font-weight: bold; }
  .page .page-jumbotron-container .jumbo-stamp-image {
    min-width: 20rem;
    min-height: 20rem;
    border: 0.5rem solid #FFFFFF;
    border-radius: 20rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: absolute;
    top: 7vh;
    left: 5rem;
    background-image: url('../../assets/img/rocket.png'); }

